.DisplayItem {
	position: relative;
	border: 1px solid grey;
	border-radius: 2px;
	margin: 20px 10px;
	width: 210px;
	box-sizing: border-box;
	height: 220px;
}
label {
	display: block;
	font-size: 0.8rem;
	line-height: 1rem;
}

label.Name {
	font-size: 1rem;
	margin-bottom: 0.8rem;
}

img {
	width: 100%;
	height: 100%;
}

.Info {
	position: absolute;
	background-color: black;
	opacity: 0.7;
	color: white;
	bottom: 0;
	left: 0;
	width: calc(100% - 19px);
	padding: 10px;
	border-bottom-left-radius: 2px;
	border-bottom-right-radius: 2px;
}

@media (max-width: 500px) {
	.DisplayItem {
		width: 100%;
		height: 300px;
		margin: 15px auto;
	}
	label {
		font-size: 1rem;
		line-height: 1rem;
	}

	label.Name {
		font-size: 1.3rem;
		line-height: 1.1rem;
		margin-bottom: 1.1rem;
	}
}
